const FilterBar = () => {
  return (
    <div className="bg-white p-4 shadow-md flex flex-col md:flex-row gap-4 justify-between">
      <input type="text" placeholder="Search Here" className="border p-2 rounded w-full md:w-1/3" />
      <select className="border p-2 rounded w-full md:w-1/4">
        <option>Choose Minority Type</option>
      </select>
      <select className="border p-2 rounded w-full md:w-1/4">
        <option>Choose Price Range</option>
      </select>
      <button className="bg-[#D16831] text-white px-6 py-2 rounded">Search Here</button>
    </div>
  );
};

export default FilterBar;
